




























































































































































































#dataCenter{
  width:100%;
  min-width: 1200px;
  min-height:100%;
  background: #efeeee;
  background-size: 100% 100%;
  display:flex;
  flex-direction:column;
  padding-bottom: 10px;
  #content{
    text-align: center;
    height:calc(100vh - 120px);
    .dataDiv{
      padding: 0 30px;
      border-bottom: 1px solid #b7b6b6;
      border-top: 1px solid #545454;
      margin-top: 10px;
      ul{
        width: 100%;
        display: flex;
        margin:2px 0;
        li{
          flex:1;
          display: flex;
          padding:10px;
          border-left: 1px solid #c2c1c1;
          &:last-child{
            border-right: 1px solid #c2c1c1;
          }
          >div{
            width:100%;
            background: #fff;
            border-radius: 5px;
            >img{
              float: left;
              padding: 11px;
              width:70px;
              margin-left: 15px;
            }
            >div{
              padding:10px;
              p{
                margin-top: 3px;
                strong{
                  font-size: 20px;
                  font-weight: bold;
                  margin-right: 10px;
                }
              }
            }
          }
        }
      }
    }
    .statistical{
      position: relative;
      width: 100%;
      height:calc(100% - 100px);
      padding:0 30px;
      margin: 0;
      .sta_box{
        position: absolute;
        height: 50%;
        width: 50%;
        padding:15px 20px;
        .con_box{
          height: 100%;
          width: 100%;
          background: #fff;
          border-radius: 10px;
          box-shadow: 0 0 5px 3px rgba(0,0,0,0.1);
          position: relative;
          overflow: hidden;
          .stateTitle {
            display: inline-block;
            position: absolute;
            top: 15px;
            left: 14px;
            width: 22px;
            height: 22px;
            &.stateTitle1 {
              background: url("../../assets/redLine/sprite.png") 0 0 no-repeat;
              background-size: cover;
            }
            &.stateTitle3 {
              width: 24px;
              height: 24px;
              background: url("../../assets/redLine/sprite.png") 0 -32px no-repeat;
              background-size: cover;
            }
            &.stateTitle4 {
              width: 24px;
              height: 24px;
              background: url("../../assets/redLine/sprite.png") 0 -63px no-repeat;
              background-size: cover;
            }
          }
          .boxBtn{
            position: absolute;
            right: 20px;
            top:10px;
            z-index: 80;
          }
          .bigBox{
            width:calc(100vw - 60px);
            height:100%;
            .smallBox{
              width:50%;
              height:100%;
              float: left;
           }
          }
          .smallToBig {
            .smallBox {
              width: 100%;
            }
          }
        }
      }
      /* box放大 */
      .activeBox{
        left:0!important;
        top:0!important;
        padding: 15px 30px!important;
        width: 100%;
        height: 100%;
        z-index: 99;
        transition: all 0.8s ease;
      }
    }
  }

  /*底部内容的样式*/
  #footer{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 27px;
    background:#fff;
    line-height: 27px;
    box-shadow: 0 0 15px 5px rgba(100,100,100,0.05);
    p{
      text-align: center;
      font-size: 12px;
      color:#999;
    }
  }
}
